import React from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { Navbar, Nav, Container, Row, NavDropdown} from 'react-bootstrap';
import { LinkContainer } from 'react-router-bootstrap'
import { NavLink } from 'react-router-dom'
import SearchBox from './SearchBox'
import { logout}from '../actions/userActions'
import { useNavigate } from 'react-router-dom'

function Header() {

    const userLogin = useSelector(state=>state.userLogin)
    const { userInfo } = userLogin
    const dispatch = useDispatch()
    const navigate = useNavigate()

    const logoutHandler = () => {
        dispatch(logout())
        navigate('/')
    }

  return (
    <header>
        <Navbar bg="primary" variant="dark" expand="lg" collapseOnSelect>
        <Container>
            <LinkContainer to ='/'>
                <Navbar.Brand> <img src='/images/logo.png' alt='logo' width='100'/>
                </Navbar.Brand>
            </LinkContainer>
            <Navbar.Toggle aria-controls="basic-navbar-nav" />
            <Navbar.Collapse id="basic-navbar-nav">
            <Nav className='m-auto fs-5'>
                <LinkContainer to ='/skincare' className='me-2'>
                <Nav.Link>Skincare</Nav.Link>
                </LinkContainer>
                <LinkContainer to ='/makeup' className='me-2'>
                <Nav.Link>Makeup</Nav.Link>
                </LinkContainer>
            </Nav>
            
            <Nav>
                <div className="d-none d-lg-block">
                <SearchBox />
                </div>
                <LinkContainer to ='/cart'>
                <Nav.Link className='me-2 d-none d-lg-block'><i className='fas fa-shopping-cart fa-xl'></i></Nav.Link>
                </LinkContainer>

                {userInfo ? (
                    <NavDropdown className='fs-5 d-none d-lg-block' title={userInfo.name} id='username' style={{width: "3vw"}}>
                        <LinkContainer to ='/profile'>
                            <NavDropdown.Item> Profile </NavDropdown.Item>
                        </LinkContainer>
                        <NavDropdown.Item onClick={logoutHandler}> Logout </NavDropdown.Item>
                    </NavDropdown>
                ):
                <LinkContainer to ='/login'>
                    <Nav.Link className='d-none d-lg-block'><i className='fas fa-user fa-xl'></i></Nav.Link>
                </LinkContainer>}
                <div className="d-lg-none fs-5">
                    <LinkContainer to ='/cart'>
                    <Nav.Link>Cart</Nav.Link>
                    </LinkContainer>
                    {userInfo ? (
                    <>
                    <LinkContainer to ='/profile'>
                        <Nav.Link> Profile </Nav.Link>
                    </LinkContainer>
                    <Nav.Link onClick={logoutHandler}> Logout </Nav.Link>
                    </>
                    ):
                    <LinkContainer to ='/login'>
                        <Nav.Link>Login</Nav.Link>
                    </LinkContainer>}
                    <SearchBox />
                </div>
            </Nav>
            </Navbar.Collapse>
        </Container>
        </Navbar>
    </header>
  )
}

export default Header