import React, { useState, useEffect} from 'react'
import { Link, useNavigate } from 'react-router-dom'
import { Form, Button, Row, Col, Table} from 'react-bootstrap'
import { LinkContainer } from 'react-router-bootstrap'
import { useDispatch, useSelector} from 'react-redux'
import { Container } from 'react-bootstrap'

import Loader from '../components/Loader'
import Message from '../components/Message'
import { getUserDetails, updateUserProfile } from '../actions/userActions'
import {USER_UPDATE_PROFILE_RESET} from '../constants/userConstants'
import { listMyOrders } from '../actions/orderActions'

function ProfileScreen() {
    const [name, setName] = useState('')
    const [email, setEmail] = useState('')
    const [password, setPassword] = useState('')
    const [confirmPassword, setConfirmPassword] = useState('')
    const [message, setMessage] = useState('')

    const dispatch = useDispatch()
    const navigate = useNavigate()

    const userDetails = useSelector(state=> state.userDetails)
    const { error, loading, user } = userDetails

    const userLogin = useSelector(state=> state.userLogin)
    const { userInfo } = userLogin

    const userUpdateProfile = useSelector(state=> state.userUpdateProfile)
    const { success } = userUpdateProfile

    const orderLists = useSelector(state=> state.orderLists)
    const { loading:loadingOrders, error: errorOrders, orders } = orderLists
    

    useEffect(()=>{
        if(!userInfo){
            navigate('/login')
        }else{
            if(!user || !user.name || success){
                dispatch({type:USER_UPDATE_PROFILE_RESET})
                dispatch(getUserDetails('profile'))
                dispatch(listMyOrders())
            }else{
                setName(user.name)
                setEmail(user.email)
            }
        }
    }, [dispatch, userInfo, user, success])

    const submitHandler = (e) => {
        e.preventDefault()
        
        if(password !== confirmPassword){
            setMessage('Passwords do not match')
        }else{
            dispatch(updateUserProfile({
                'id': user._id,
                'name': name,
                'email': email,
                'password': password,
            }))
            setMessage('')
        }
    }
  return (
    <Container className='py-4'>
    <Row>
        <Col md={3}>
            <h2>User Profile</h2>
            {message && <Message variant='danger'>{message}</Message>}
            {error && <Message variant='danger'>{error}</Message>}
            {loading && <Loader />}
            <Form onSubmit={submitHandler}>
                <Form.Group controlId='name' className='mt-3'>
                    <Form.Label>Name</Form.Label>
                    <Form.Control
                        required
                        type='name'
                        placeholder='Enter name'
                        value={name}
                        onChange={(e)=>setName(e.target.value)}></Form.Control>
                </Form.Group>

                <Form.Group controlId='email' className='mt-3'>
                    <Form.Label>Email</Form.Label>
                    <Form.Control
                        required
                        id="exampleInputEmail1"                   
                        type='email'
                        placeholder='Enter email'
                        value={email}
                        onChange={(e)=>setEmail(e.target.value)}></Form.Control>
                </Form.Group>

                <Form.Group controlId='password' className='mt-3'>
                    <Form.Label>Password</Form.Label>
                    <Form.Control
                        type='password'
                        placeholder='Enter Password'
                        value={password}
                        onChange={(e)=> setPassword(e.target.value)}
                    >
                    </Form.Control>
                </Form.Group>

                <Form.Group controlId='confirmPassword' className='mt-3'>
                    <Form.Label>Confirm Password</Form.Label>
                    <Form.Control
                        type='password'
                        placeholder='Confirm Password'
                        value={confirmPassword}
                        onChange={(e)=> setConfirmPassword(e.target.value)}
                    >
                    </Form.Control>
                </Form.Group>

                <Button className='mt-3 button-block' type='submit' variant='primary'>Update</Button>
        </Form>
        </Col>

        <Col className='ms-5'>
            <h2>My Orders</h2>
            {loadingOrders ? (
                <Loader />
            ): errorOrders ? (
                <Message variant='danger'>{errorOrders}</Message>
            ): <Table striped responsive className='table-sm'>
                    <thead>
                        <tr>
                            <th>ID</th>
                            <th>Date</th>
                            <th>Total</th>
                            <th>Paid</th>
                            <th></th>
                        </tr>
                    </thead>
                    <tbody className='plain-text'>
                        {orders.map(order =>(
                            <tr key={order._id}>
                                <td>{order._id}</td>
                                <td>{order.createdAt.substring(0,10)}</td>
                                <td>$ {order.totalPrice}</td>
                                <td>{order.isPaid? order.paidAt.substring(0,10) : <i className='fas fa-times'></i>}</td>
                                <td><LinkContainer to={`/order/${order._id}`}>
                                    <Button className='btn btn-sm m-1'>Details</Button>
                                </LinkContainer></td>
                            </tr>
                        ))}
                    </tbody>
                </Table>}
        </Col>
    </Row>
    </Container>
  )
}

export default ProfileScreen