import React, { useState, useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { Row, Col, Container } from 'react-bootstrap'
import Product from '../components/Product'
import Loader from '../components/Loader'
import Message from '../components/Message'
import { listFilterProducts } from '../actions/productActions';
import Slider from 'rc-slider';

function MakeupScreen() {
    const dispatch = useDispatch()
    const productList = useSelector(state => state.productList)
    const {error, loading, products} = productList
    const [category, setCategory] = useState([]);
    const [minPrice, setMinPrice] = useState(0);
    const [maxPrice, setMaxPrice] = useState(100);
    const [sortBy, setSortBy] = useState('');
    const option = 'makeup'

    useEffect(() => {
        dispatch(listFilterProducts(option, category, minPrice, maxPrice, sortBy));
      }, [dispatch, option, category, minPrice, maxPrice, sortBy]);

    const handleCategoryChange = (selectedCategory) => {
        setCategory(selectedCategory);
    };
  

  return (
    <Container className='py-4'>
        <Row className='products'>
            <Col md={3} className='left'>
                <div className='filterItem'>
                    <h3>Product Categories</h3>
                    <div className='inputItem'>
                        <button 
                            type="button" 
                            class="btn btn-success fs-5"
                            id='1'
                            value='All'
                            onClick={() => setCategory([])}>All</button>
                    </div>
                    <div className='inputItem'>
                        <button 
                            type="button" 
                            class="btn btn-outline-success fs-5"
                            id='2'
                            value='Face'
                            onClick={() => handleCategoryChange('Face')}>Face</button>
                    </div>
                    <div className='inputItem'>
                        <button 
                            type="button" 
                            class="btn btn-outline-success fs-5"
                            id='3'
                            value='Eyes'
                            onClick={() => handleCategoryChange('Eyes')}>Eyes</button>
                    </div>
                    <div className='inputItem'>
                        <button 
                            type="button" 
                            class="btn btn-outline-success fs-5"
                            id='4'
                            value='Lips'
                            onClick={() => handleCategoryChange('Lips')}>Lips</button>
                    </div>
                </div>

                <div className="filterItem">
                    <h3>Filter by price</h3>
                    <div className="inputItem" style={{ "width": "70%" }}>
                        <Slider
                        min={0}
                        max={100}
                        step={10}
                        value={[minPrice, maxPrice]}
                        onChange={(values) => {
                            setMinPrice(values[0]);
                            setMaxPrice(values[1]);
                        }}
                        range
                        trackStyle={{backgroundColor:'green'}} 
                        />
                        <span>${minPrice}</span>
                        <span> - ${maxPrice}</span>
                    </div>
                </div>

                <div className="filterItem">
                    <h3>Sort by</h3>
                    <div className="inputItem">
                        <input
                        type="radio"
                        id="sortLowest"
                        value="lowest"
                        name="sort"
                        checked={sortBy === 'lowest'}
                        onChange={(e) =>
                            setSortBy(e.target.value)}
                        />
                        <label htmlFor="sortLowest" className='ms-4'>Price: Lowest First</label>
                    </div>
                    <div className="inputItem">
                        <input
                        type="radio"
                        id="sortHighest"
                        value="highest"
                        name="sort"
                        checked={sortBy === 'highest'}
                        onChange={(e) =>
                            setSortBy(e.target.value)}
                        />
                        <label htmlFor="sortHighest" className='ms-4'>Price: Highest First</label>
                    </div>
                    <div className="inputItem">
                        <input
                        type="radio"
                        id="sortNewest"
                        value="newest"
                        name="sort"
                        checked={sortBy === 'newest'}
                        onChange={(e) =>
                            setSortBy(e.target.value)}
                        />
                        <label htmlFor="sortNewest" className='ms-4'>Newest</label>
                    </div>
                </div>


            </Col>

            <Col md={9}>
                <Row>
                {(
                    products.map((product) => (
                        <Col key={product._id} md={9} lg={6} xl={4}>
                        <Product product={product} />
                        </Col>
                    ))
                )}
                </Row>
            </Col>
        </Row>
    </Container>
  )
}

export default MakeupScreen